@media (max-width: 1200px) {

    .container {
        padding: 0 5px 0 5px;
    }

    .promo__wrapper {
        grid-template-columns: 560px 415px;
    }

    .projects__wrapper {
        justify-content: space-around;
    }
}

@media (max-width: 992px) {
    .menu {
        width: 480px;
        margin-left: 51px;
        &__item a {
            font-size: 15px;
        }
    }

    .promo {
        &__wrapper {
            grid-template-columns: 394px 354px;
        }
        &__text {
            font-size: 40px;
        }
    }
}

@media (max-width: 768px) {
    .menu {
        margin: 0;
        justify-content: space-around;
        &__item a {
            font-size: 13px;
        }
    }
    .promo {
        &__wrapper {
            grid-template-columns: 315px 248px;
        }
        &__text {
            font-size: 32px;
        }
    }
    .stack {
        margin-top: 70px;
        &__item img {
            width: 70px;
        }
    }
    .projects {
        margin-bottom: 50px;
        &__title {
            margin-top: 60px;
            font-size: 32px;
        }
        .product {
            margin-top: 20px;
        }
    }
}

@media (max-width: 576px) {
    .header {
        &__wrapper {
            display: block;
            .logo {
                display: flex;
                justify-content: center;
            }
            .menu {
                margin: 10px auto;
                width: 300px;
            }
            .icons {
                margin: 10px auto 0;
            }
        }
    }
    .promo {
        margin-top: 15px;
        &__wrapper {
            display: block;
        }
        &__text {
            font-size: 29px;
        }
    }
    .product {
        width: 300px;
        &__wrapper-text {
            padding: 5px;
        }
        &__title {
            font-size: 23px;
        }
        &__description {
            font-size: 15px;
        }
    }
}
