.projects {
    margin-bottom: 247px;
    &__title {
        margin-top: 133px;
        font-weight: 700;
        font-size: 52px;
        text-align: center;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}


.product {
    margin-top: 66px;
    width: 387px;
    box-shadow: 0px 13px 109px #d9d9d9;
    border-radius: 15px;
    &__picture {
        img {
            object-fit: contain;
        }
    }
    &__wrapper-text {
        padding: 24px 44px 26px 36px;
    }
    &__title {
        font-weight: 500;
        font-size: 28px;
    }
    &__description {
        margin-top: 15px;
        font-weight: 300;
        font-size: 18px;
        color: #666666;
    }
    &__stack {
        margin-top: 15px;
        font-weight: 300;
        font-size: 14px;
        color: #42446E;
        span {
            font-weight: 400;
            font-size: 16px;
        }

    }
    &__prewiew {
        margin-top: 19px;
        display: flex;
        img {
            width: 20px;
            height: 20px;
        }
        a {
            margin-left: 10px;
            text-decoration: underline;
            color: #000000;
        }
    }
}
