html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Bold.eot");
  src: url("../fonts/Poppins-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Poppins-Bold.woff") format("woff"), url("../fonts/Poppins-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.eot");
  src: url("../fonts/Poppins-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Poppins-Medium.woff") format("woff"), url("../fonts/Poppins-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Regular.eot");
  src: url("../fonts/Poppins-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Poppins-Regular.woff") format("woff"), url("../fonts/Poppins-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Light.eot");
  src: url("../fonts/Poppins-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/Poppins-Light.woff") format("woff"), url("../fonts/Poppins-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "DM Sans";
  src: url("../fonts/DMSans24pt-Medium.eot");
  src: url("../fonts/DMSans24pt-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/DMSans24pt-Medium.woff2") format("woff2"), url("../fonts/DMSans24pt-Medium.woff") format("woff"), url("../fonts/DMSans24pt-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.header__wrapper {
  margin-top: 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu {
  width: 609px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 227px;
  margin-right: 51px;
}
.menu__item a {
  color: #666666;
  font-family: "DM Sans";
  font-weight: 500;
  font-size: 20px;
}

.icons {
  width: 130px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.promo {
  margin-top: 72px;
}
.promo__wrapper {
  display: grid;
  grid-template-columns: 655px 488px;
  align-items: center;
  justify-content: space-between;
}
.promo__text {
  font-weight: 700;
  font-size: 58px;
  color: #42446E;
}
.promo__text span {
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right top, #13B0F5, #E70FAA);
}

.stack {
  margin-top: 122px;
}
.stack__wrapper {
  display: flex;
  justify-content: space-between;
}

.projects {
  margin-bottom: 247px;
}
.projects__title {
  margin-top: 133px;
  font-weight: 700;
  font-size: 52px;
  text-align: center;
}
.projects__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.product {
  margin-top: 66px;
  width: 387px;
  box-shadow: 0px 13px 109px #d9d9d9;
  border-radius: 15px;
}
.product__picture img {
  object-fit: contain;
}
.product__wrapper-text {
  padding: 24px 44px 26px 36px;
}
.product__title {
  font-weight: 500;
  font-size: 28px;
}
.product__description {
  margin-top: 15px;
  font-weight: 300;
  font-size: 18px;
  color: #666666;
}
.product__stack {
  margin-top: 15px;
  font-weight: 300;
  font-size: 14px;
  color: #42446E;
}
.product__stack span {
  font-weight: 400;
  font-size: 16px;
}
.product__prewiew {
  margin-top: 19px;
  display: flex;
}
.product__prewiew img {
  width: 20px;
  height: 20px;
}
.product__prewiew a {
  margin-left: 10px;
  text-decoration: underline;
  color: #000000;
}

@media (max-width: 1200px) {
  .container {
    padding: 0 5px 0 5px;
  }
  .promo__wrapper {
    grid-template-columns: 560px 415px;
  }
  .projects__wrapper {
    justify-content: space-around;
  }
}
@media (max-width: 992px) {
  .menu {
    width: 480px;
    margin-left: 51px;
  }
  .menu__item a {
    font-size: 15px;
  }
  .promo__wrapper {
    grid-template-columns: 394px 354px;
  }
  .promo__text {
    font-size: 40px;
  }
}
@media (max-width: 768px) {
  .menu {
    margin: 0;
    justify-content: space-around;
  }
  .menu__item a {
    font-size: 13px;
  }
  .promo__wrapper {
    grid-template-columns: 315px 248px;
  }
  .promo__text {
    font-size: 32px;
  }
  .stack {
    margin-top: 70px;
  }
  .stack__item img {
    width: 70px;
  }
  .projects {
    margin-bottom: 50px;
  }
  .projects__title {
    margin-top: 60px;
    font-size: 32px;
  }
  .projects .product {
    margin-top: 20px;
  }
}
@media (max-width: 576px) {
  .header__wrapper {
    display: block;
  }
  .header__wrapper .logo {
    display: flex;
    justify-content: center;
  }
  .header__wrapper .menu {
    margin: 10px auto;
    width: 300px;
  }
  .header__wrapper .icons {
    margin: 10px auto 0;
  }
  .promo {
    margin-top: 15px;
  }
  .promo__wrapper {
    display: block;
  }
  .promo__text {
    font-size: 29px;
  }
  .product {
    width: 300px;
  }
  .product__wrapper-text {
    padding: 5px;
  }
  .product__title {
    font-size: 23px;
  }
  .product__description {
    font-size: 15px;
  }
}
html {
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}