.promo {
    margin-top: 72px;
    &__wrapper {
        display: grid;
        grid-template-columns: 655px 488px;
        align-items: center;
        justify-content: space-between;
    }
    &__text {
        font-weight: 700;
        font-size: 58px;
        color: #42446E;
        span {
            color: transparent;
            -webkit-background-clip: text;
            background-clip: text;
            background-image: linear-gradient(to right top, #13B0F5, #E70FAA);
        }
    }
}
